import { BASE_IMAGE_URL,getChuOrder,confirmShipping } from '@/utils/api'
import uniPopup from '@/components/uni-popup.vue'
import uniLoadMore from '@/components/uni-load-more.vue';
export default {
	components: {
		uniLoadMore,
		uniPopup
	},
	data() {
		return {
			loadingText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			list: [],
			loadingType: 0,
			page: 1,
			typeList: ['待发货', '待收货', '已完成'],
			curType: 0,
			type:'',
			express_name: '',
			express_no: '',
			actionIndex: 0
		}
	},
	methods: {
		changeType(index){
			this.curType = index;
			this.page = 1;
			this.list = [];
			this.getList();
		},
		hidePop(){
			this.type = '';
		},
		async getList(){
			let res = await getChuOrder(this.curType, this.page);
			uni.stopPullDownRefresh();
			if(res.code == 1000){
				if(this.page == 1){
					this.list = res.data;
				}else{
					this.list = this.list.concat(res.data);
				}
				
				if(res.data.length < 10){
					this.loadingType = 2;
				}else{
					this.loadingType = 0;
				}
			}
		},
		setExpress(index){
			this.express_name = "";
			this.express_no = "";
			this.type = 'middle';
			this.actionIndex = index;
		},
		
		async confirmShipping(index){
			let params = {
				o_id: this.list[this.actionIndex].o_id,
				o_express_name: this.express_name,
				o_express_no: this.express_no
			};
			let res = await confirmShipping(params);
			if(res.code == 1000){
				uni.showToast({
					icon: 'none',
					duration: 1000,
					title: "操作成功"
				});
				this.type = '';
				this.getList();
			}
		}
	},
	onReachBottom() {
		this.loadingType = 1;
		this.page++;
		this.getList();
	},
	
	onPullDownRefresh() {
		console.log("dddddd");
		this.page = 1;
		this.list = [];
		this.getList();
	},
	onLoad(){
		
		this.getList();
	}
	
}
